export const sendMessageToGemini = async (userMessage: string): Promise<string> => {
  return new Promise(resolve => {
    setTimeout(() => {
      const lower = userMessage.toLowerCase().trim();

      // 1. Bonjour / accueil
      if (lower === '' || lower === 'bonjour' || lower.includes('salut') || lower.includes('hello')) {
        resolve(
          "Bonjour et bienvenue sur le portfolio d'Evle Kouamlan. " +
          "Je peux rpondre  vos questions sur ses comptences, ses projets, son exprience et la faon dont il travaille."
        );
        return;
      }

      // 2. Comptences
      if (lower.includes('comptence') || lower.includes('skills') || lower.includes('techno')) {
        resolve(
          "Evle matrise React, TypeScript, Tailwind CSS et Vite pour le front-end. " +
          "Pour le back-end, il utilise Node.js, Express, MongoDB et JWT. " +
          "Il travaille aussi avec Git et GitHub pour la gestion de version et le dploiement."
        );
        return;
      }

      // 3. Projets / portfolio
      if (lower.includes('projet') || lower.includes('portfolio') || lower.includes('site')) {
        resolve(
          "Son portfolio prsente plusieurs projets : une application bancaire front-end, " +
          "un site vitrine responsive et ce portfolio React avec un assistant IA. " +
          "Chaque projet met en avant le code propre, l'accessibilit et le responsive."
        );
        return;
      }

      // 4. Parcours / exprience
      if (lower.includes('exprience') || lower.includes('parcours') || lower.includes('histoire')) {
        resolve(
          "Evle a commenc comme formateur de franais langue trangre, puis s'est form au dveloppement web. " +
          "Il travaille aujourd'hui sur des projets front-end et full-stack en utilisant les technologies modernes du JavaScript."
        );
        return;
      }

      // 5. Tarifs / prix
      if (lower.includes('prix') || lower.includes('tarif') || lower.includes('budget') || lower.includes('combien')) {
        resolve(
          "Les tarifs dpendent du type de projet, de sa complexit et de la dure de la mission. " +
          "Evle s'aligne sur les pratiques habituelles des dveloppeurs freelance en France et adapte son prix au besoin rel. " +
          "Pour un montant prcis, il est prfrable de le contacter et de lui prsenter votre projet."
        );
        return;
      }

      // 6. Contact
      if (lower.includes('contact') || lower.includes('email') || lower.includes('mail') || lower.includes('joindre')) {
        resolve(
          "Pour le contacter, vous pouvez utiliser les liens indiqus sur ce portfolio, " +
          "par exemple son profil GitHub ou LinkedIn, ou le moyen de contact mentionn dans la section ddie."
        );
        return;
      }

      // 7. Question gnrale
      resolve(
        "Je suis l'assistant d'Evle Kouamlan. Vous pouvez me poser des questions sur ses comptences, ses projets, " +
        "son parcours ou ses tarifs, et je vous donnerai des informations pour vous aider  mieux le connatre."
      );
    }, 1200); // dlai raliste
  });
};